import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../modules/user/userservices/authentication.service';
import { DevotionalitemserviceService } from './devotionalitemservice.service';

@Component({
    selector: 'app-devotional',
    templateUrl: './devotional.component.html',
    styleUrls: ['./devotional.component.css']
})
export class DevotionalComponent implements OnInit {

    isSubscriber: Boolean = false;
    itemInfo:Object[];

    private currentUser :object ;

    constructor(
        private authenticationService: AuthenticationService,
        private devotionalItemService:DevotionalitemserviceService
    ) {
        this.currentUser = this.authenticationService.currentUserValue;
        if (this.currentUser) {
            this.isSubscriber = true;
        }
    }

    ngOnInit() {
        this.devotionalItemService.getItems()
        .subscribe(
            (data)=>{
                this.itemInfo = data;
            }
        );
    }

}
