import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { DevotionalComponent } from './devotional/devotional.component';
import { SelectivePreloadingStrategy } from './selective-preloading-strategy';
import { AuthGuard } from './modules/user/authguard/auth.guard';

const routes: Routes = [
    { path: '', redirectTo: 'home', pathMatch: 'full' },
    { path: 'home', component: HomeComponent },
    { path: 'devotional', component: DevotionalComponent },
    { path: 'user', loadChildren: "./modules/user/user.module#UserModule" },
    { path: 'admin', loadChildren: "./modules/admin/admin.module#AdminModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'cashier', loadChildren: "./modules/cashier/cashier.module#CashierModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'devotional/books', loadChildren: "./modules/devotionalbook/devotionalbook.module#DevotionalbookModule", data: {preload: true} },
    { path: 'devotional/multimedia', loadChildren: "./modules/devotionalmultimedia/devotionalmultimedia.module#DevotionalmultimediaModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'devotional/market', loadChildren: "./modules/devotionalmarket/devotionalmarket.module#DevotionalmarketModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'devotional/eventmanagement', loadChildren: "./modules/devotionaleventmanagement/devotionaleventmanagement.module#DevotionaleventmanagementModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'devotional/health', loadChildren: "./modules/devotionalhealth/devotionalhealth.module#DevotionalhealthModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: 'devotional/wealth', loadChildren: "./modules/devotionalwealth/devotionalwealth.module#DevotionalwealthModule", data: {preload: false}, canActivate: [AuthGuard] },
    { path: '**', component: PagenotfoundComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes,{preloadingStrategy: SelectivePreloadingStrategy})],
    exports: [RouterModule],
    providers:[SelectivePreloadingStrategy]
})
export class AppRoutingModule { }
