import { Injectable, OnInit, Input, Inject, Optional } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of, pipe } from 'rxjs';
import { catchError, map, tap, flatMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ServiceRequesterService{
 
  private _nonceObject : Object;
  private _paramObject: Object; 
  private _apiUrl : string = "https://kavindraknit.000webhostapp.com/api/";//'http://127.0.0.1:4001/wordpress/api/';//

  constructor(
    private _http: HttpClient
  ) { }

  getNonce(str:string,param:Object,ctrl:String):Observable<any>{
    //console.log('here in init service method');
    const url = this._apiUrl + 'get_nonce/?controller='+ctrl+'&method='+str;
    this._paramObject = param;
    return this._http.get<any>(url)
    .pipe(
      tap(
        data=> {
          this._nonceObject = data;
        }
      ),
      flatMap(_=>this.getLoginData()),
      catchError(this.handleError('getUser', []))
    );
  }

  private getLoginData():Observable<any>{
    //console.log('here in calling service method');
    let paramString = '';
    for(var i in this._paramObject){
        paramString += (i +'=' + this._paramObject[i]+"&");
    }
    const url = this._apiUrl + ''+this._nonceObject['controller'] + '/' + this._nonceObject['method'] + '/?' + paramString + 'nonce=' + this._nonceObject['nonce'];
    return this._http.get<any>(url)
      .pipe(
        tap(data => console.log("received data is " + JSON.stringify(data)) ),
        catchError(this.handleError('getUser', []))
    );
  }

  //getRestData(data,'devtrip.dev@gmail.com','Asdfg@123')
   /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
 
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
 
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);
 
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
